
.song-list {
  float: left;
  width: 850px;
  transition: all .5s;
}
.song-list tr {
  border-bottom: 1px dashed #999;
  cursor: pointer;
}
.song-list tr:hover {
  background: rgba(200, 100, 50, 0.5);
}
.song-list tr:nth-child(2n) {
  background: rgba(0, 0, 0, 0.2);
}
.song-list td,
.song-list th {
  height: 40px;
}
/* media */
/* 窗口宽度<960,设计宽度=768 */
@media screen and (max-width: 959px) {
.song-list {
    width: 510px;
}
}
/* 窗口宽度<768,设计宽度=640 */
@media screen and (max-width: 767px) {
.song-list {
    float: none;
    width: 640px;
}
}
