
.control {
  float: right;
  width: 350px;
  border-radius: 15px;
  text-align: center;
  line-height: 80px;
  background: rgba(0, 0, 0, 0.5);
}
.control .pre,
.control .next {
  display: inline-block;
  vertical-align: middle;
  width: 30px;
  height: 30px;
  line-height: 30px;
  margin: 0 20px;
  cursor: pointer;
}
.control .pre i,
.control .next i {
  display: inline-block;
}
.control .pre i:nth-child(1),
.control .next i:nth-child(1) {
  width: 4px;
  height: 30px;
  border-radius: 15px;
  background: #fde;
}
.control .pre i:nth-child(2),
.control .next i:nth-child(2) {
  width: 0;
  height: 0;
  border-radius: 5px;
  border: 15px solid transparent;
  border-right: 15px solid #fde;
  margin-left: -22px;
}
.control .next {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.control .pause-btn,
.control .play-btn {
  display: inline-block;
  vertical-align: middle;
  width: 34px;
  height: 34px;
  cursor: pointer;
}
.control .pause-btn i,
.control .play-btn i {
  position: absolute;
  display: inline-block;
  width: 30px;
  height: 4px;
  background: #fde;
  -webkit-transform-origin: left;
          transform-origin: left;
  transition: all 1s;
}
.control .pause-btn {
  text-align: left;
}
.control .pause-btn i {
  display: inline-block;
  width: 30px;
  height: 4px;
  background: #fde;
  -webkit-transform-origin: left;
          transform-origin: left;
  transition: all 1s;
}
.control .pause-btn i:nth-child(1) {
  -webkit-transform: translateX(5px) rotate(90deg);
          transform: translateX(5px) rotate(90deg);
}
.control .pause-btn i:nth-child(2) {
  -webkit-transform: translateX(5px) rotate(30deg);
          transform: translateX(5px) rotate(30deg);
}
.control .pause-btn i:nth-child(3) {
  -webkit-transform: translate(5px, 30px) rotate(-30deg);
          transform: translate(5px, 30px) rotate(-30deg);
}
.control .play-btn i:nth-child(1) {
  background: transparent;
}
.control .play-btn i:nth-child(2) {
  -webkit-transform: rotate(90deg) translateY(-10px);
          transform: rotate(90deg) translateY(-10px);
}
.control .play-btn i:nth-child(3) {
  -webkit-transform: rotate(90deg) translateY(10px);
          transform: rotate(90deg) translateY(10px);
}
.control .progress {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  width: 200px;
  height: 10px;
  background: #ddd;
}
.control .progress i {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  height: 10px;
  background: #f00;
}
.control .duration {
  color: #fff;
  margin-left: 50px;
}
/* 窗口宽度<960,设计宽度=768 */
/* 窗口宽度<768,设计宽度=640 */
@media screen and (max-width: 767px) {
.control {
    float: none;
    width: 640px;
}
}
/* 窗口宽度<640,设计宽度=480 */
@media screen and (max-width: 639px) {
.control {
    float: none;
    width: 480px;
}
}
/* 窗口宽度<480,设计宽度=320 */



