
.f-cb:after,
.f-cbli li:after {
  display: block;
  clear: both;
  visibility: hidden;
  height: 0;
  overflow: hidden;
  content: ".";
}
.slide-fade-enter-active {
  transition: all 0.8s ease;
}
.slide-fade-leave {
  transition: all 0.8s ease-in-out;
}
.slide-fade-enter,
.slide-fade-leave-active {
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
  opacity: 0;
}
#app {
  width: 1280px;
  margin: 50px auto 0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  transition: all .5s;
}
#app .main-nav {
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ddd;
}
#app .main-nav img,
#app .main-nav input {
  margin: 0 15px;
  vertical-align: middle;
}
#app .main-nav input {
  width: 470px;
  height: 30px;
  border: 1px solid #ddd;
  background: #F2F2F5;
  padding: 0 1em;
}
#app .main-nav button {
  display: none;
  float: right;
  width: 50px;
  height: 50px;
  border: 2px solid #F2F2F5;
  border-radius: 5px;
  background: #F2F2F5;
}
#app .main-nav button i {
  display: inline-block;
  width: 40px;
  height: 3px;
  background: #ddd;
  border-radius: 5px;
}
#app .main-nav .nav-bar {
  position: relative;
  float: right;
}
#app .main-nav .nav-bar button,
#app .main-nav .nav-bar i,
#app .main-nav .nav-bar li,
#app .main-nav .nav-bar a,
#app .main-nav .nav-bar ul {
  transition: all 1s;
}
#app .main-nav li {
  display: inline-block;
  width: 80px;
  height: 50px;
  text-align: center;
}
#app .main-nav li a {
  display: block;
  height: 50px;
}
#app .main-nav li .router-link-exact-active.active {
  background: orange;
  color: #fff;
}
#app .main-nav li:hover {
  background: orange;
}
#app .main-nav li:hover a {
  color: #eee;
}
#app .main-nav .wei {
  display: none;
  width: 0;
  height: 0;
  border: 25px solid #999;
  border-bottom: 25px solid transparent;
}
/* 窗口宽度<960,设计宽度=768 */
@media screen and (max-width: 959px) {
#app {
    width: 768px;
}
#app .main-nav input {
    width: 270px;
}
}
/* 窗口宽度<768,设计宽度=640 */
@media screen and (max-width: 767px) {
#app {
    width: 640px;
    margin: 0;
    overflow: hidden;
}
#app .main-nav input {
    width: 250px;
}
#app .nav-bar:hover ul {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
}
#app .nav-bar:hover li {
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
}
#app .nav-bar:hover button {
    border: none;
    border-radius: 0;
    background: rgba(0, 0, 0, 0.5);
}
#app .nav-bar:hover i:nth-child(1) {
    -webkit-transform: scaleX(1.41421356) rotate(45deg);
            transform: scaleX(1.41421356) rotate(45deg);
}
#app .nav-bar:hover i:nth-child(2) {
    opacity: 0;
}
#app .nav-bar:hover i:nth-child(3) {
    -webkit-transform: scaleX(1.41421356) rotate(-45deg);
            transform: scaleX(1.41421356) rotate(-45deg);
}
#app .nav-bar ul {
    position: absolute;
    top: 50px;
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-perspective: 200px;
            perspective: 200px;
}
#app .nav-bar li {
    width: 46px;
    border-left: 2px dashed orange;
    border-right: 2px dashed orange;
    background: rgba(25, 25, 25, 0.2);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
}
#app .nav-bar .wei {
    display: block;
}
#app .nav-bar button {
    display: inline-block;
}
#app .nav-bar button i {
    -webkit-transform-origin: left;
            transform-origin: left;
}
}
/* 窗口宽度<640,设计宽度=480 */
@media screen and (max-width: 639px) {
#app {
    width: 480px;
}
#app .main-nav .nav-bar {
    width: 50px;
    border-radius: 5px;
}
}
/* 窗口宽度<480,设计宽度=320 */
@media screen and (max-width: 479px) {
#app {
    width: 320px;
}
#app .main-nav input {
    width: 150px;
}
}



